import {
	useState,
	useEffect,
	useRef,
	useImperativeHandle,
	forwardRef,
} from "react";
import Form from "./../FormData/Form";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faTrashCan,
	faSearch,
	faUserEdit,
	faQuestion,
} from "@fortawesome/free-solid-svg-icons";
import { FixedSizeGrid as Grid } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import { Db } from "../../../database/Db_new";
import {
	splitArray,
	GetDatasAt,
	UpdateDataAt,
	DeleteAt,
} from "../../../helper";
import styles from "./../main.module.scss";
import { toast } from "react-toastify";
import ViewDetail from "./../ViewDetail";
import QTest from "./Test";
const GUTTER_SIZE = 16;
const toastBottomPos = {
	POSITION: toast.POSITION.BOTTOM_CENTER,
};

// hàm lọc giá trị
async function filterData(Type, str, flag = "global") {
	let filterFn = (item) => {
		let parent = item[Type].split(" ");
		let strArray = str.split(" ");
		if (Type === "hanzi") {
			parent = [...item.hanzi];
			strArray = [...str];
			return parent.some((v) => strArray.includes(v));
		}
		return parent.some((v) => {
			return strArray.includes(v.toLowerCase());
		});
	};
	if (flag === "global")
		return Db.transaction("rw", Db.datas, async function () {
			let data = await Db.datas.orderBy("_id").filter(filterFn).toArray();
			return Promise.resolve(data);
		});
	let cacheDt = JSON.parse(localStorage.getItem("cache-data"))?.filter(
		filterFn
	);
	return Promise.resolve(cacheDt || []);
}

const Item = (data, style, props) => {
	return (
		<>
			{data && (
				<div
					key={data._id}
					style={{
						...style,
						left: style.left + GUTTER_SIZE,
						top: style.top + GUTTER_SIZE,
						width: style.width - GUTTER_SIZE,
						height: style.height - GUTTER_SIZE,
					}}
					className={styles["card"]}
				>
					<div
						className={styles["content"]}
						onClick={() => {
							props.viewDetailRef.current?.setViewDetail(data);
						}}
					>
						<div className={styles["front"]}>{data.hanzi}</div>
						<div className={styles["back"]}>
							<div>
								<div>{data.pinyin}</div>
								<div>{data.han_viet}</div>
								<div>{data.Vietnamese_meaning}</div>
							</div>
							<button
								onClick={(e) => {
									e.stopPropagation();
									UpdateDataAt("tags", "_id", {
										key: props.tag._id,
										field: "QTestArrayID",
										newValue: props.tag.QTestArrayID.filter(
											(id) => id !== data._id
										),
									}).then(() => {
										DeleteAt("datas", "_id", data._id).then(
											() => {
												toast.info(
													`deleted : ${data._id}`,
													toastBottomPos
												);
												GetDatasAt("datas", "tag_id", {
													key: props.tag._id,
												}).then(props.setDatas);
											}
										);
									});
								}}
							>
								<FontAwesomeIcon
									icon={faTrashCan}
									color={"var(--BrightRed"}
								/>
							</button>
						</div>
					</div>
				</div>
			)}
		</>
	);
};

function TagScreen(props, ref) {
	// luu gia tri nhan duoc khi click item cua tags ben sidebar
	// tag giup xac dinh target luu data (them tu moi vao dung nhom)
	const [tag, setTag] = useState("");
	const listRef = useRef();
	const [toggleMode, setToggleMode] = useState("viewer");

	let [datas, setDatas] = useState([]);
	useImperativeHandle(ref, () => ({
		setTag,
	}));
	useEffect(() => {
		GetDatasAt("datas", "tag_id", { key: tag._id }).then(setDatas);
	}, [tag, toggleMode]);
	useEffect(() => {
		localStorage.setItem("cache-data", JSON.stringify(datas));
		return () => {
			localStorage.setItem("cache-data", JSON.stringify([]));
		};
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [tag, datas]);
	let viewDetailRef = useRef();
	return (
		<>
			<div className={styles["wrapper"]}>
				{toggleMode === "viewer" && (
					<>
						<ViewDetail ref={viewDetailRef} />
						<div ref={listRef} className={styles["list-wrapper"]}>
							<AutoSizer>
								{({ height, width }) => {
									let numcol = 5;
									if (width < 920) {
										numcol = 4;
									}
									if (width < 645) {
										numcol = 3;
									}
									if (width < 375) {
										numcol = 2;
									}
									// convert 1d array to numcol d
									let reshapeData = splitArray(datas, numcol);
									return (
										<Grid
											className='List'
											columnWidth={
												width / numcol -
												GUTTER_SIZE / numcol
											}
											rowHeight={height / 2}
											innerElementType={forwardRef(
												({ style, ...rest }, ref) => (
													<div
														ref={ref}
														style={{
															...style,
															paddingLeft:
																GUTTER_SIZE,
															paddingTop:
																GUTTER_SIZE,
														}}
														{...rest}
													/>
												)
											)}
											rowCount={reshapeData.length}
											columnCount={numcol}
											height={height}
											width={width}
										>
											{({
												columnIndex,
												rowIndex,
												style,
											}) => {
												let data =
													reshapeData[rowIndex][
														columnIndex
													];
												return Item(data, style, {
													viewDetailRef,
													tag,
													setDatas,
												});
											}}
										</Grid>
									);
								}}
							</AutoSizer>
							{}
						</div>
					</>
				)}
				{toggleMode === "addData" && (
					<div className={styles["form-wrapper"]}>
						<Form tag={tag} setToggleMode={setToggleMode} />
					</div>
				)}
				{toggleMode === "test" && (
					<QTest
						tag={tag}
						datas={datas}
						props={{
							toggleMode,
							setToggleMode,
						}}
					/>
				)}
				{/* ẩn thanh dưới khi mode là addData và test */}
				{toggleMode === "viewer" && (
					<div className={styles["btns"]}>
						<div className={styles["wrapper"]}>
							{!(toggleMode === "test") && (
								<button
									title={
										toggleMode !== "addData"
											? "new data"
											: ""
									}
									onClick={() => {
										setToggleMode("addData");
									}}
								>
									<FontAwesomeIcon icon={faUserEdit} />
								</button>
							)}
							{!(toggleMode === "addData") && (
								<button
									onClick={() => {
										setToggleMode("test");
									}}
								>
									<FontAwesomeIcon icon={faQuestion} />
								</button>
							)}
							{!(
								toggleMode === "test" ||
								toggleMode === "addData"
							) && (
								<>
									<div className={styles["search-wrapper"]}>
										<form
											onSubmit={(e) => {
												e.preventDefault();

												const data = Array.from(
													new FormData(
														e.target
													).entries()
												).reduce(
													(memo, [key, value]) => ({
														...memo,
														[key]: value,
													}),
													{}
												);
												let s_value = data[
													"search-value"
												]
													.trim()
													.toLowerCase();
												if (s_value) {
													if (
														data["search-wrapper"]
													) {
														// default , search in global
														console.log("global");

														filterData(
															data["search-type"],
															s_value
														).then(setDatas);
													} else {
														// search in local
														console.log("local");
														filterData(
															data["search-type"],
															s_value,
															"local"
														).then(setDatas);
													}
												}
											}}
										>
											<div>
												<select name='search-type'>
													<option
														value='hanzi'
														defaultValue='hanzi'
													>
														hanzi
													</option>
													<option value='pinyin'>
														pinyin
													</option>
													<option value='Vietnamese_meaning'>
														Vietnamese
													</option>
												</select>
												<label>
													<input
														type='checkbox'
														defaultChecked={true}
														name='search-wrapper'
													/>{" "}
													-g
												</label>
											</div>

											<div
												className={
													styles["input-wrapper"]
												}
											>
												<input
													type='search'
													name='search-value'
													placeholder='search ...'
												/>
												<button
													type='submit'
													data-title={`search with : ${
														document.querySelector(
															"input[name=search-value]"
														)?.value || null
													}`}
												>
													<FontAwesomeIcon
														icon={faSearch}
													/>
												</button>
											</div>
										</form>
										<button
											onClick={() => {
												GetDatasAt("datas", "tag_id", {
													key: tag._id,
												}).then(setDatas);
											}}
										>
											re-load
										</button>
									</div>
									<label className={styles["slide-input"]}>
										<input
											onChange={(e) => {
												listRef?.current.style.setProperty(
													"--Hanzi_FontSize",
													`${e.target.value}rem`
												);
											}}
											type='range'
											defaultValue={2.5}
											step={0.5}
											min={1}
											max={5}
										/>
									</label>
								</>
							)}
						</div>
					</div>
				)}
			</div>
		</>
	);
}

export default forwardRef(TagScreen);
