import {
	useState,
	useContext,
	useCallback,
	useImperativeHandle,
	forwardRef,
} from "react";
import { mainContext } from "../../../App";
import HanziComponent from "../../HanziComponent";
import { UpdateDataAt } from "../../../helper";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faXmarkCircle, faEye } from "@fortawesome/free-solid-svg-icons";
import styles from "./styles.module.scss";
import checkbox from "./../checkbox.module.scss";
function ViewDetail({ fn }, ref) {
	let Context = useContext(mainContext);
	let [data, setData] = useState({});
	let words = data?.hanzi ? [...data.hanzi] : [];
	useImperativeHandle(ref, () => {
		return { setViewDetail: setData };
	});
	const HanziComponents = useCallback(
		(words, config) => {
			if (data?.hanzi) {
				if (words.length > 1) {
					return words.map((word, index) => {
						return (
							<HanziComponent
								key={index}
								character={word}
								showPinyin={config.showPinyin}
								options={Context.hanziComponentOptions}
							/>
						);
					});
				} else
					return (
						<HanziComponent
							character={words[0]}
							showPinyin={config.showPinyin}
							options={Context.hanziComponentOptions}
						/>
					);
			}
			return null;
		},
		[data] // eslint-disable-next-line react-hooks/exhaustive-deps
	);

	return (
		data?.hanzi && (
			<div className={styles["ViewDetail-wrapper"]}>
				<div className={styles["header"]}>
					<button
						className={styles["close-form"]}
						onClick={() => {
							UpdateDataAt("datas", "_id", {
								key: data._id,
								field: "view_count",
								newValue: data.view_count + 1,
							}).then(() => {
								setData({});
							});
						}}
					>
						<FontAwesomeIcon icon={faXmarkCircle} />
					</button>
				</div>
				<div className={styles["wrapper"]}>
					<div className={styles["HanziComponents-wrapper"]}>
						{HanziComponents(words, { showPinyin: false })}
					</div>

					<div className={styles["content-wrapper"]}>
						<ul className={styles["list"]}>
							<li
								style={{ cursor: "pointer" }}
								onClick={() => {
									const utterThis =
										new SpeechSynthesisUtterance(
											data.hanzi
										);
									utterThis.lang = "zh-CN";
									utterThis.volume = 1;
									speechSynthesis.speak(utterThis);
								}}
							>
								hanzi : <span>{data.hanzi}</span>{" "}
							</li>
							<li>
								pinyin : <span>{data.pinyin}</span>{" "}
							</li>
							<li>
								Han Viet : <span>{data.han_viet}</span>
							</li>
							<li>
								Vietnamese :{" "}
								<span>{data.Vietnamese_meaning}</span>
							</li>
						</ul>
						<div
							className={styles["note"]}
							dangerouslySetInnerHTML={{ __html: data.note }}
						></div>
					</div>
				</div>
				<div className={styles["footer"]}>
					<span>
						learned{" "}
						<input
							className={checkbox["regular-checkbox"]}
							type='checkbox'
							id='checkbox-1-2'
							defaultChecked={data.is_a_learned_word}
							onChange={(e) =>
								UpdateDataAt("datas", "_id", {
									key: data._id,
									field: "is_a_learned_word",
									newValue: e.target.checked,
								})
							}
						/>
						<label htmlFor='checkbox-1-2'></label>
					</span>

					<span title='View count'>
						<FontAwesomeIcon
							icon={faEye}
							color='hsl(163, 72%, 41%)'
						/>{" "}
						{data.view_count}
					</span>
				</div>
			</div>
		)
	);
}
export default forwardRef(ViewDetail);
