import { useState, useEffect, useRef, useLayoutEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faXmark } from "@fortawesome/free-solid-svg-icons";
import styles from "./../main.module.scss";
import { Db } from "../../../database/Db_new";
import { UpdateDataAt, splitArray, shuffleArr } from "../../../helper";
import { nanoid } from "nanoid";

const Q_template = (prop, type) => {
	let lang = type === "c-v" ? "vietnamese" : "chinese";
	return {
		_id: prop._id,
		type: type,
		ui: "Translate",
		data: prop,
		q: `translate [ ${
			prop[type === "c-v" ? "hanzi" : "Vietnamese_meaning"]
		} ] to ${lang}`,
		answer: `${prop[type === "c-v" ? "Vietnamese_meaning" : "hanzi"]}`,
	};
};
//tempQTestArrayID
const genQuestion = (_datas, config) => {
	if (config.listRender) {
		let translateGroup = [
			["Vietnamese_meaning", "hanzi"],
			["pinyin", "hanzi"],
		];
		let typeLen = translateGroup.length;
		let datas = [[], []];
		for (let i = 0; i < config.row; ++i) {
			let row = Math.floor(Math.random() * typeLen);
			datas[0][i] = {
				a: _datas[i][translateGroup[row][0]],
				match: _datas[i][translateGroup[row][1]],
			};
			datas[1][i] = {
				b: _datas[i][translateGroup[row][1]],
				match: _datas[i][translateGroup[row][0]],
			};
		}

		shuffleArr(datas[0]);
		shuffleArr(datas[1]);
		let Datas = {
			QuestionList: [],
			type: "Chooses",
			rows: config.row,
			isFinal: config.isFinal,
		};
		for (let i = 0; i < datas[0].length; ++i) {
			Datas.QuestionList.push({
				a: { q: datas[0][i].a, match: datas[0][i].match },
				b: { q: datas[1][i].b, match: datas[1][i].match },
			});
		}
		return Datas;
	}
	let random = Math.random();
	let q = undefined;
	for (let i = 0; i < _datas.length; i++) {
		if (!config.tempQTestArrayID.current.includes(_datas[i]._id))
			q = _datas[i];
	}
	if (!q) return;
	return random >= 0.5 ? Q_template(q, "c-v") : Q_template(q, "v-c");
};
const check = (Q, A) => {
	console.log(Q.answer);
	if (!Q.answer.trim()) return { isOk: true, match: 100 };
	let Q_answer = [...Q.answer.trim().toLowerCase()].filter(
		(i) => ![" ", ","].includes(i)
	);
	let my_answer = [...A.trim().toLowerCase()].filter(
		(i) => ![" ", ","].includes(i)
	);
	let threshold = Q_answer.length * 0.65;
	let count = 0;
	let posMatch = 0;

	if (Q.type === "c-v") {
		for (let i = 0; i < my_answer.length; i++) {
			if (Q_answer.includes(my_answer[i])) {
				count++;
			}
		}
	}
	if (Q.type === "v-c") {
		if (!Q.data.Vietnamese_meaning.trim())
			return { isOk: true, match: 100 };
		Q_answer.filter((item) => {
			if (my_answer.includes(item)) {
				count++;
				return true;
			}
			return false;
		});
	}
	for (let i = 0; i < my_answer.length; i++) {
		if (Q_answer[i] === my_answer[i]) posMatch++;
	}
	if (posMatch === Q_answer.length) return { isOk: true, match: 100 };
	if (count > threshold) {
		return {
			isOk: true,
			match: ~~((count / Q_answer.length) * 100),
		};
	}

	return {
		isOk: false,
		match: ~~((count / Q_answer.length) * 100),
	};
};

function QRow(props) {
	let { data, style } = props;
	return (
		<li
			style={{
				...style,
			}}
			className={styles["T_row"]}
		>
			<div
				data-match={data.a.match}
				onClick={(e) => {
					e.stopPropagation();
					props.match(e);
				}}
				className={styles["Item"]}
			>
				{data.a.q}
			</div>
			<div
				data-match={data.b.q}
				onClick={(e) => {
					e.stopPropagation();
					props.match(e);
				}}
				className={styles["Item"]}
			>
				{data.b.q}
			</div>
		</li>
	);
}
const QuestionUIs = {
	Translate: function (props) {
		return (
			<div style={{ width: "50%" }}>
				<div className={styles["T-text"]}>{props.datas?.q}</div>
				<div className={styles["T-form"]}>
					<form onSubmit={props.handleSubmit}>
						<input type='search' name='answer' />
					</form>
				</div>
			</div>
		);
	},
	Chooses: function (props) {
		const QuestionLen = props.datas.QuestionList.length;
		let correct = useRef(0);
		let choosed = useRef(null);
		useEffect(() => {
			return () => {
				correct.current = 0;
				choosed.current = null;
			};
		}, []);
		const match = (event) => {
			if (choosed.current) {
				event.target.style.border = "2px solid var(--BrightRed)";
				let match =
					choosed.current.dataset.match ===
					event.target.dataset.match;
				if (match) {
					event.target.style.border = "none";
					choosed.current.style.border = "none";
					event.target.style.backgroundColor = "#07bc0c";
					choosed.current.style.backgroundColor = "#07bc0c";
					choosed.current = null;
					correct.current++;
					if (
						correct.current >= QuestionLen &&
						!props.datas.isFinal
					) {
						let id = setTimeout(() => {
							props.nextQuestion();
							clearTimeout(id);
						}, 1500);
					}
				} else {
					let id = setTimeout(() => {
						event.target.style.border = "none";
						choosed.current.style.border = "none";
						choosed.current = null;
						clearTimeout(id);
					}, 1000);
				}
			} else {
				event.target.style.border = "2px solid var(--BrightRed)";
				choosed.current = event.target;
			}
		};
		return (
			<div className={styles["T-wrapper"]}>
				<form onSubmit={(e) => e.preventDefault()}>
					{splitArray(props.datas.QuestionList, 5).map((list) => {
						return (
							<ul key={nanoid(5)} className={styles["T-list"]}>
								{list.map((item) => {
									return (
										<QRow
											key={nanoid(5)}
											data={item}
											style={{ height: "calc(100% /5)" }}
											match={match}
										></QRow>
									);
								})}
							</ul>
						);
					})}
				</form>
				{props.datas.isFinal && (
					<button
						className={styles["T-submit"]}
						type='submit'
						onClick={() => {
							if (correct.current >= QuestionLen) {
								correct.current = 0;
								props.reset(true);
							} else {
								console.log("not complited yet!");
							}
						}}
					>
						submit
					</button>
				)}
			</div>
		);
	},
};
export default function QTest({ tag, datas, props }) {
	const [isReset, setIsReset] = useState(false);
	const [Question, setQuestion] = useState({ ui: "Translate" });
	const TestRef = useRef();
	const tempQTestArrayID = useRef([]);
	// ui mac dinh
	const QuestionUi = useRef({ UI: QuestionUIs[Question?.ui || "Translate"] });
	useLayoutEffect(() => {
		console.log("reset");
		QuestionUi.current = { UI: QuestionUIs[Question?.ui || "Translate"] };
	}, [isReset, Question?.ui]);
	useEffect(() => {
		if (isReset) {
			QuestionUi.current = { UI: QuestionUIs["Translate"] };
		}
		(async () => {
			let tags = await Db["tags"].orderBy("id").toArray();
			return tags.filter((item) => item._id === tag._id)[0].QTestArrayID;
		})().then((QTestdArr) => {
			tempQTestArrayID.current = QTestdArr;
			let isComplited = QTestdArr.length === datas.length;
			if (isComplited) {
				setIsReset(true);
			} else setQuestion(genQuestion(datas, { tempQTestArrayID }));
		});

		return async () => {
			await UpdateDataAt("tags", "_id", {
				key: tag._id,
				field: "QTestArrayID",
				newValue: tempQTestArrayID.current,
			}).then(() => {
				tempQTestArrayID.current = [];
			});
		};
	}, []);

	const handleSubmit = (event) => {
		event.preventDefault();
		const answer = Array.from(new FormData(event.target).entries())[0][1];
		let result = check(Question, answer);
		TestRef.current.dataset.match = `match : ${result.match} %`;
		TestRef.current.style.border = `2px solid ${
			result.isOk ? "var(--LimeGreen)" : "var(--BrightRed)"
		}`;
		if (result.isOk) tempQTestArrayID.current.push(Question._id);
		let id = setTimeout(() => {
			clearTimeout(id);
			TestRef.current.style.border = "2px solid var(--boder-color)";
			TestRef.current.dataset.match = `match : 0 %`;
			if (tempQTestArrayID.current.length === datas.length) {
				QuestionUi.current = { UI: QuestionUIs["Chooses"] };
				setQuestion(
					genQuestion(datas, {
						isFinal: true,
						listRender: true,
						row: datas.length,
					})
				);
				return;
			}
			if (result.isOk) {
				let random = Math.floor(Math.random() * datas.length);
				switch (random) {
					case 2:
					case 4:
					case 6:
						QuestionUi.current = { UI: QuestionUIs["Chooses"] };
						setQuestion(
							genQuestion(datas, {
								isFinal: false,
								listRender: true,
								row: random,
							})
						);
						break;
					default:
						setQuestion(genQuestion(datas, { tempQTestArrayID }));
				}
			}
			event.target.reset();
		}, 1500);
	};
	const progress = isReset
		? 100
		: ~~((tempQTestArrayID.current.length / datas.length) * 100);
	const RtButton = (
		<button
			type='reset'
			onClick={() => {
				UpdateDataAt("tags", "_id", {
					key: tag._id,
					field: "QTestArrayID",
					newValue: [],
				}).then(() => {
					tempQTestArrayID.current = [];
					setIsReset(false);
					setQuestion(genQuestion(datas, { tempQTestArrayID }));
				});
			}}
		>
			reset
		</button>
	);

	return (
		<div className={styles["test-wrapper"]}>
			<div
				className={styles["test-container"]}
				ref={TestRef}
				data-match={0}
				data-progress={progress}
			>
				<button
					className={styles["close-btn"]}
					title={props.toggleMode !== "addData" ? "form data" : ""}
					onClick={() => {
						props.setToggleMode("viewer");
					}}
				>
					<FontAwesomeIcon icon={faXmark} />
				</button>
				{isReset ? (
					<>
						<h3>test complited!</h3>
						{RtButton}
					</>
				) : (
					// eslint-disable-next-line react/jsx-pascal-case
					<QuestionUi.current.UI
						datas={Question}
						handleSubmit={handleSubmit}
						reset={setIsReset}
						nextQuestion={() => {
							setQuestion(
								genQuestion(datas, { tempQTestArrayID })
							);
						}}
					/>
				)}
			</div>
		</div>
	);
}
